/* ImageDetail.css */
.image-detail {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #000000;
    color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 1000;
    
  }

  .title{
    margin-top: 11vh;
    text-align: center;
    font-size: 20px;
    display: inline-block;
    font-weight:bold;
  overflow: hidden;
  white-space: nowrap;
  border-right: 2px solid; /* Cursor effect */
  animation: typing 5s steps(200, end), blink-caret 0.5s step-end infinite;
  letter-spacing: 1px;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  }

  .description{
    text-align: center;
    font-size: 11px;
    display: inline-block;
  overflow: hidden;
  white-space: nowrap;
  border-right: 2px solid; /* Cursor effect */
  animation: typing 10s steps(200, end), blink-caret 0.5s step-end infinite;
  font-family:  Arial, sans-serif;
  }

  @keyframes typing {
    from { width: 0; }
    to { width: 100%; }
  }
  
  @keyframes blink-caret {
    from, to { border-color: transparent; }
    50% { border-color: black; }
  }

  .tilt-image-container {
    perspective: 1000px; /* Adds depth to the 3D effect */
  }
  
  .full-image {
    margin-top:80px;
    width: 80vw; /* Make the image responsive to the viewport width */
    object-fit: contain; /* Maintain aspect ratio */
    
    transition: transform 0.1s ease-out; /* Smooth transition */
    transform-style: preserve-3d;
  }
  
  .back-button {
    position: absolute;
    bottom: 45px;
    left: 45px;
    padding: 8px 22px;
    font-size: 20px;
    cursor: pointer;
    color: black;
    background: white;
    border: white solid 1px;
    border-radius: 30px;

    letter-spacing: .8px;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  }

  .next-button {
    position: absolute;
    bottom: 45px;
    right: 45px;
    padding: 8px 22px;
    font-size: 20px;
    cursor: pointer;
    color: black;
    background: white;
    border: white solid 1px;
    border-radius: 30px;
 
    letter-spacing: .8px;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
    
  }